@use 'hero';

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

body {
    background: linear-gradient(#fff, orange, yellow);
}

.container {

    @media (max-width:640px) {
        padding: 8px;
        max-width: 80%;
    }

    @media (max-width:1024px) {
        max-width: 90%;
    }
}

.container-content {
    display:flex;
    margin: 48px auto;
    max-width: 600px;

    @media (max-width:640px) {
        margin: 24px auto;
        max-width: 80%;
        width: 100%;

        .content-left {
            &__title {
                font-size: 18px;
                white-space: nowrap;
            }
            &__list {
            &__item {
                font-size: 14px;
            }
        }
    }
    .content-right {
        &__img {
            margin-top: 42px;
            margin-left: -24px;
        }
    }

    .content-left {
        &__title {
            font-size: 32px;
            margin-bottom: 8px;
        }
        &__list {
            margin-top: 16px;
            list-style: none;
        &__item {
            padding: 8px;
        }
        }
    }
    .content-right {
        display: flex;
        align-items: center;
        &__img {
            height: 100px;
        }
    }
}
}